@import "../../../../styles/utils/colors";

.Navbar {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Navbar-Link {
  margin: 0 1em;
  padding: .5em 1em;
  position: relative;
  text-decoration: none;
  &.active {
    &::after {
      content: '';
      display: block;
      width: 100%;
      height: 2px;
      background: $secondary;
      left: 0;
      bottom: 0;
      position:absolute;
    }
  }
  &::after {
    content: '';
    display: block;
    width: 0;
    height: 2px;
    background: $secondary;
    transition: all 100ms ease-in-out;
    left: 50%;
    bottom: 0;
    position:absolute;
  }
  &:hover::after {
    width: 100%;
    left: 0;
  }
  &:visited {
    color: $secondary;
  }
}
